.parallax-content {
  padding: 20px;
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 800px) {
  .location-background {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    z-index: 1;
    /* display: flex; */
  }

  .left {
    position: relative;
    background-color: #2d9f8f;
    width: 100%;
    height: 50vh;
  }

  /* Control the right side */
  .right {
    background-image: url('../images/location2-mobile.png');
    background-size: cover;
    height: 50vh;
    width: 100%;
    /* background-size: 100% auto; */
    /* border: 4px solid black; */
  }

  .location-body {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background: rgba(76, 125, 119, 0.2);
    display: flex;
    flex-direction: column;
    z-index: 1;
    display: flex;
  }

  .help-location-text {
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: max(4vw, 34px);
    line-height: 120%;
    color: #ffffff;
  }

  .help-location-text2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: max(1.5vw, 16.5px);
    line-height: 120%;
    color: #ffffff;
  }
}

@media only screen and (min-width: 800px) {
  .location-background {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background: #ffffff;
    display: flex;
    flex-direction: row;
    z-index: 1;
    display: flex;
  }

  .split {
    height: 100vh;
    width: 50%;
    top: 0;
    overflow-x: hidden;
  }

  .left {
    position: relative;
    background-image: url('../images/locations.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 50%;
  }

  /* Control the right side */
  .right {
    position: relative;
    width: 50%;
    background-image: url('../images/location2-mobile.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: bottom;
  }

  .location-body {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background: rgba(76, 125, 119, 0.2);
    display: flex;
    flex-direction: column;
    z-index: 1;
    display: flex;
    justify-content: flex-end;
  }

  .location-body2 {
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    z-index: 1;
    display: flex;
  }

  .help-location-text {
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: max(4vw, 34px);
    line-height: 120%;
    color: #ffffff;
  }

  .help-location-text2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: max(1.5vw, 16.5px);
    line-height: 120%;
    color: #ffffff;
  }

  .parallax-content {
    display: flex;
    flex-direction: column;
  }
}
