.ticket-background {
  position: relative;
  /* width: 1440px; */
  /* min-height: 10000px; */
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background: #2d9f8f;
  display: flex;
  flex-direction: row;
  z-index: 1;
  overflow: hidden;
}

.ticket-background-mobile {
  background: #092342;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  height: 100vh;
  width: 100%;
}

.split {
  position: fixed;
  height: 100%;
  width: 50%;
  top: 0;
  overflow-x: hidden;
  overflow-y: hidden;
}

.ticket-img-box {
  position: relative;
  left: 0;
}

.ticket-text-box {
  right: 0;
  width: 90vw;
}

.ticket-box {
  width: 84%;
  box-sizing: border-box;
}

.ticket-box-bottom {
  height: 84%;
  margin: -67px 56px 0 56px;
  border: 10px solid #ffffff;
  border-top: none;
}

.ticket-box-top {
  height: 95.22%;
  margin: 5vh 3vw 0 3vw;
  border: 10px solid #ffffff;
  border-bottom: none;
}

.ticket-loop {
  border: 5px solid #ffffff;
  box-sizing: border-box;
  border-radius: 13px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 63.47%;
  height: 12.6%;
  margin-top: 10%;
  bottom: 0;
}

.ticket-offset {
  width: 63.47%;
  height: 12.6%;
  margin-top: 10%;
  bottom: 0;
}

.ticket-text-box {
  min-height: 9.5%;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.ticket-text {
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  margin: 0px;
}

h1.ticket-text {
  font-size: max(10vw, 76px);
  font-weight: normal;
}

h3.ticket-text {
  font-weight: bold;
  font-size: max(5vw, 50px);
}

h5.ticket-text {
  font-size: max(2vw, 20px);
  font-weight: bold;
  margin-top: 3vh;
}

p.ticket-text {
  /* font-size: 24px; */
  /* line-height: 28px; */
  font-weight: normal;
}

@media only screen and (max-width: 800px) {
  .text-container {
    color: white;
    width: 90vw;
    display: flex;
    flex-direction: column;
    padding: 15px;
  }

  h1.text-container {
    font-size: 36px;
  }

  p.text-container {
    font-size: 18px;
  }

  .ticket-text-box {
    color: white;
  }

  h1.ticket-text {
    font-size: 36px;
    font-weight: normal;
  }

  p.ticket-text {
    font-size: 18px;
    font-weight: normal;
  }

  h5.ticket-text {
    font-weight: 700;
    font-size: 20px;
  }

  h3.ticket-text {
    font-weight: 700;
    font-size: 33px;
  }
}
