.stats-body {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #2d9f8f;
  display: flex;
  flex-direction: column;
  z-index: 1;
  display: flex;
}

.stats-ellipse-open {
  position: absolute;
  width: 32.62px;
  height: 32.62px;
  border: 3px solid #ffffff;
  flex: 1;
}

.stats-ellipse-full {
  position: absolute;
  width: 32.62px;
  height: 32.62px;
  background: #ffffff;
  flex: 1;
}

.stats-text {
  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  font-weight: 300;
  color: #ffffff;
  align-self: center;
  text-align: center;
}
.stats-main-text {
  font-size: max(10vw, 60px);
}

.stats-secondary-text {
  font-size: calc(10px + 1.8vw);
  /* flex-grow: 1; */
  margin: 0;
  color: #ffffff;
  align-self: center;
  text-align: center;
}

.stats-tertiary-text {
  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: calc(8px + 1.4vw);
  margin: 0;
  color: #ffffff;
  align-self: center;
  text-align: center;
}

.stats-image {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0.44;
  object-fit: cover;
}

.stats-lower-container {
  height: 60%;
  background: #2d9f8f;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.stats-image-container {
  height: 45%;
  position: relative;
  text-align: center;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.stats-lower-elements {
  height: 100%;
  display: flex;
  flex: 10;
  flex-direction: column;
  margin-top: 30px;
}

.stats-icon {
  display: flex;
  flex-direction: column;
  padding: 20px;
  position: relative;
  align-items: center;
  line-height: 2;
}

p {
  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: max(2vw, 20px);
  color: #ffffff;
}

hr {
  align-self: center;
  display: block;
  height: 1px;
  border: 0;
  border-top: 3px solid #ffffff;
  margin: 1em 0;
  padding: 0;
  width: 50%;
}

.stats-container {
  width: max(100px, calc(30px + 6vw));
  height: max(100px, calc(30px + 6vw));
  display: flex;
  align-items: center;
  justify-content: center;
}

.stats-border-container {
  border-color: white;
  border-style: solid;
  border-radius: 50%;
  background-color: white;
}

.stats-outline-container {
  border-color: white;
  border-width: 2px;
  border-style: solid;
  border-radius: 50%;
}

.source {
  position: absolute;
  left: 1.75%;
  top: 90%;

  font-size: 13px;
  line-height: 15px;
}
